<template>
  <div id="user" class="content">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="setting" v-loading="loading">
      <el-form ref="form" :model="form" label-width="180px">
        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.channelRate"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.systemRate"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.platformScale"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.minOrderAmount"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.userReturnScale"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.merchantReturnDays"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.userReturnAmount"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.recommendUserDays"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.recommendMerchantDays"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.recommendUserScale"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.recommendMerchantScale"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.addDays" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>

          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.withdrawDate"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.withdrawWeeks"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.minRechargeAmount"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.minWithdrawUser"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.maxWithdrawUser"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.feeUser"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.minWithdrawMerchant"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.maxWithdrawMerchant"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.feeMerchant"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-switch on-color="#13ce66" off-color="#f7ba2a" on-text="" off-text="" v-model="form.isDirectReturnRecommend"></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-switch on-color="#13ce66" off-color="#f7ba2a" on-text="" off-text="" v-model="form.isCreateNonPaymentOrder"></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-switch on-color="#13ce66" off-color="#f7ba2a" on-text="" off-text="" v-model="form.isCreateAlikeUserOrder"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-switch on-color="#13ce66" off-color="#f7ba2a" on-text="" off-text="" v-model="form.isAutoAudit"></el-switch>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-switch on-color="#13ce66" off-color="#f7ba2a" on-text="" off-text="" v-model="form.isAutoClearing"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="9">
            <el-form-item label="">
              <el-radio-group v-model="form.withdrawRule">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              <el-input-number v-model="form.intervalWeeksNumber" placeholder="" size="small" max="0" class="input-num"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="form.bank"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="IP">
          <el-input type="textarea" :rows="5" placeholder="IP" v-model="form.ipWhitelists"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button @click="onCancel"></el-button>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        form: {},
        loading: false
      }
    },
    created: function () {
      this.form = this.$store.state.customer
      this.form.isDisabled = this.form.isDisabled !== 1
    },
    methods: {
      onSubmit () {
        this.loading = true
        this.$axios.post(SITEURL.MEMBER_EDIT_URL, {id: this.form.id, isDisabled: this.form.isDisabled, password: this.form.password}).then(response => {
          this.loading = false
          this.$message({
            showClose: true,
            type: 'success',
            message: '' + response.data.message
          })
          this.$router.go(-1)
        }).catch(error => {
          this.loading = false
          console.log(error)
        })
      },
      onCancel () {
        this.$router.go(-1)
      },
      handleCurrentChange (val) {
        this.currentPage = val
        console.log(`: ${val}`)
      }
    }
  }
</script>

<style scoped>
  .breadcrumb {
    margin: 20px 0 20px 0;
  }
  .setting {
    display: block;
    width: 90%;
    border: 1px solid #eaeefb;
    border-radius: 4px;
    transition: .2s;
    padding: 24px;
  }
</style>



