<template>
  <div id="scale" class="content">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="scale-create" v-loading="loading">
      <el-form ref="scaleForm" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="" prop="scale">
          <el-input-number v-model.number="form.scale" placeholder="" :min="0" :max="100" class="input-num"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="scaleByMerchant">
          <el-input-number v-model.number="form.scaleByMerchant" placeholder="" :min="0" :max="100" class="input-num"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="scaleByUser">
          <el-input-number v-model.number="form.scaleByUser" placeholder="" :min="0" :max="100" class="input-num"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="scaleDescribe">
          <el-input v-model="form.scaleDescribe"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="onCancel"> </el-button>
          <el-button type="info" icon="circle-check" @click="onSubmit('scaleForm')" style="width: 150px;">  </el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import * as SITEURL from '@/store/app-urls'
  export default {
    name: 'user',
    data () {
      return {
        form: {
          scale: null,
          scaleByMerchant: null,
          scaleByUser: null,
          scaleDescribe: ''
        },
        loading: false,
        rules: {
          scale: [
            { type: 'number', required: true, message: '', trigger: 'blur' }
          ],
          scaleByMerchant: [
            { type: 'number', required: true, message: '', trigger: 'blur' }
          ],
          scaleByUser: [
            { type: 'number', required: true, message: '', trigger: 'blur' }
          ],
          scaleDescribe: [
            { required: true, message: '', trigger: 'blur' },
            { min: 5, message: '5', trigger: 'blur' }
          ]
        }
      }
    },
    created: function () {
    },
    methods: {
      onSubmit (formName) {
        // 
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.loading = true
            this.$axios.post(SITEURL.MERCHANT_SCALE_CREATE, this.form).then(response => {
              this.loading = false
              if (response.data.success) {
                this.$message.success('' + response.data.message)
                this.$router.push({ path: '/setting/scale/list' })
              } else {
                this.$message.error('' + response.data.message)
              }
            }).catch(error => {
              this.loading = false
              console.log(error)
            })
          } else {
            this.loading = false
            console.log('error submit!!')
            return false
          }
        })
      },
      onCancel () {
        this.$router.go(-1)
      }
    }
  }
</script>

<style>
  .scale-create {
    display: block;
    width: 40%;
    border: 1px solid #eaeefb;
    border-radius: 4px;
    transition: .2s;
    padding: 24px;
  }
  .input-num {
    width: 100%;
  }
</style>



